$color-light-grey:#909090;
$color-dark-grey:#505050;


.news-flex {
    display: flex;
    flex-direction: column;
    max-width: 1080px; 
	margin: 0 auto !important; 
    float: none !important; 
}

.news-flex > .news-card-flex {
    flex: auto;
    margin-left: 0.75em;
    margin-right: 0.75em;
    margin-bottom: 1em;
    border-radius: 10px;
}
.news-card-flex > .news-card-row {
    flex: auto;
    display: flex;
    flex-direction: row;
    max-height: 224px;
}
.news-card-flex > .news-card-column {
    flex: auto;
    display: flex;
    flex-direction: column;
}

.news-card-flex .news-content {
    flex-grow: 3;
    overflow: hidden;
    padding-top: 0.5em;
    & > * {
        padding-right: 0.5em;
        padding-left: 0.5em;
    }
}

.news-card-flex .news-img-container {
    flex-grow: 0;
    border-radius: 10px 0 0 10px;
    max-width: 300px;
    & > .news-img {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.05);
        border-radius: 10px 0 0 10px;
        object-fit: cover;
        width: 300px;
        height: 224px;
    }
}

.news-card-flex .article-title {
    font-size: x-large;
    margin-bottom: 0.5em;
    margin-top: 0.25em;
    font-weight: bold;
}

.news-card-flex .article-header {
    font-weight: lighter;
    color:$color-light-grey;

    & > * {
        padding-left: 0.5em;
    }
}

.content-snippet {
    margin-bottom: 0.5em;
}

.news-card-flex .source-header {
    font-weight: lighter;
    color: $color-dark-grey !important;
    text-decoration: underline;
}

.load-more {
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 50%;
    margin: auto;
    font-size: 30px;
    height: 60px;
    width: 60px;
    margin-bottom: 20px;
    color: $color-dark-grey;
    * {
        margin-top: 17px;
    }
}

/* change between the small and large view */

.news-card-flex .alternative {
    display: none;
}
/* Intermediary View */
@media(max-width: 800px) {

    .news-card-flex .news-img-container {
        max-width: 250px;
        margin: 0 !important;
        max-height: 100%;
        min-width: 250px;

        & > .news-img {
            width: 90%;
            max-height: 90%;
            margin-left: 5%;
            margin-right: 5%;
            position: relative;
            top:5%;
            border-radius: 5px;
        }
    }
}

/* small view */
@media(max-width: 550px) {

    .news-card-flex .main {
        display: none;
    }

    .news-card-flex .alternative {
        display: block !important;
    }

    .alternative > * {
        margin: 0.5em;
    }

    .news-card-flex .news-img-container {
        max-width: revert;
        margin: 0 !important;
        min-width: revert;

        & > .news-img {
            width: 100%;
            border-radius: 10px 10px 0 0;
            margin: 0 !important;
            object-fit: cover;
            position: revert;
            top: revert;
        }
    }
}
